body {
  --main-color: #00acdc;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.header__logo-img {
  display: block;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  padding: 23px 0;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav__link--margin-left {
  margin-left: 20px;
}

.nav__link:hover {
  color: var(--main-color);
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.is-active {
  color: var(--main-color);
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.product-card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 16px;
  transition: transform .3s;
  display: flex;
}

.product-card:hover {
  transform: scale(1.2);
}

.product-card__image {
  width: 160px;
  height: 134px;
  margin-top: 16px;
}

.product-card__name {
  color: #060b35;
  box-sizing: border-box;
  width: 166px;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  text-decoration: none;
  transition: color .3s;
}

.product-card__code {
  color: #616070;
  box-sizing: border-box;
  width: 166px;
  margin: 4px 0 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product-card__price {
  box-sizing: border-box;
  justify-content: space-between;
  width: 166px;
  margin-top: 24px;
  display: flex;
}

.product-card__price_label {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.product-card__price_value {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-card__buy {
  color: #fff;
  text-align: center;
  box-sizing: border-box;
  background-color: #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  transition-property: color, background-color;
  transition-duration: .3s;
}

.product-card__buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.product-card__reviews {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: end;
  width: 166px;
  margin-top: 16px;
  display: flex;
}

.product-card__reviews_label {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product-card:hover .product-card__name {
  color: #34568b;
}

.stars {
  justify-content: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

html {
  font-family: Roboto, Arial, sans-serif;
}

body {
  margin: 0;
  padding: 0;
}

.catalog {
  grid-template-columns: 200px;
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}
